import React from 'react'
import styled from 'styled-components'
import {Checkbox} from 'antd-mobile'
import {Field, change} from 'redux-form'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {isInArray} from '@js/commom/GlobalUtil.web'

const CheckboxItem = Checkbox.CheckboxItem;


class CheckboxSub extends React.Component {

    constructor(props) {
        super(props);

        this.hobbys = [];
    }

    static propTypes = {
        checkboxData: PropTypes.array,
    };

    onChange = (val,item) => {
        //console.log(val);
        //console.log(item);
        const {input: {name}, meta: {form}} = this.props;
        if (!isInArray(this.hobbys,val)) {
            this.hobbys.push(val);
        }
        if (!item.target.checked) {   //取消选择
            let arr = this.hobbys.filter((ele) => {
                if (ele !== val) {
                    return ele;
                }
            });
            this.hobbys = arr;
        }
        //console.log(this.hobbys);
        this.props.changeFiledValue(form, name, this.hobbys);
    };

    render() {
        let _checkboxData = this.props.checkboxData;

        return (
            <CheckBoxView>
                {
                    _checkboxData && _checkboxData.map((ele) => {
                        return (
                            <CheckboxItem key={ele.value} onChange={(item) => this.onChange(ele.label,item)}>
                                {ele.label}
                            </CheckboxItem>
                        )
                    })
                }
            </CheckBoxView>
        )
    }
}

class CheckboxField extends React.Component {
    render(){
        return(
            <Field
                {...this.props}
                component={CheckboxSub}
            />
        )
    }
}

export default connect(
    null,
    (dispatch) => ({
        changeFiledValue: (form, filedName, value) => dispatch(change(form, filedName, value))
    })
)(CheckboxField)


const CheckBoxView = styled.div`
    &&& {
        .am-radio{
            padding: .05rem;
            border: 1px solid #ccc;
            border-radius: 50%;
        }
    }
`;


