import React from 'react'

import styled from 'styled-components'

export default class Mock extends React.Component{

    _goLink = (type) => {
        if (type === 'login') {
            this.props.history.push('/Sys/');
        } else if (type === 'recharge') {
            this.props.history.push('/Home/Recharge')
        }
    };

    render() {
        return(
            <Root>
                <ItemTitle>示例：</ItemTitle>
                <RowBox onClick={() => this._goLink('login')}>登录</RowBox>
                <RowBox onClick={() => this._goLink('recharge')}>充值列表</RowBox>
                <Text>相关说明：参考<a target="_blank" href="https://github.com/aichen28/react_template/blob/master/doc/mock.md">mock模拟数据请求</a></Text>
            </Root>
        )
    }
}

const Root = styled.div`
    width: 100%;
    height: 100%;
`;

const Text = styled.p`
    color: #999999;
    line-height: .4rem;
    text-indent: .3rem;
    a{color: #11b6f8;}
`;

const ItemTitle = styled.div`
    height: .8rem;
    line-height: .8rem;
    padding-left: .3rem;
`;

const RowBox = styled.div`
    height: .8rem;
    line-height: .8rem;
    background-color: #fff;
    margin-bottom: .1rem;
    padding: 0 .3rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    label{color: #999999}
    ::after{
        content: '';
        width: .16rem;
        height: .29rem;
        margin-top: .25rem; 
        display: block;
        background: url(${props => props.imgUrl || require('@js/resources/arrow-right@2x.png')}) no-repeat center;
        background-size: 100%;
    }
`;


