import React from 'react';

import styled from 'styled-components';

import {ActivityIndicator} from 'antd-mobile'

import {
    refreshAble
} from '@js/component/index.web'

import {
    requestRechargeList
} from './reducers/HomeActions'


class Recharge extends React.Component {

    componentDidMount() {
        this._onRefresh();
    }

    componentWillUnmount() {
        this.props.fetchDataCancle()
    }

    _onRefresh = () => {
        this.props.fetchData(requestRechargeList());
    };

    render() {
        //console.log(this.props.data);
        let data = this.props.data;
        return (
            <Root>
                <HeadBox>
                    <li>充值金额</li>
                    <li>充值时间</li>
                </HeadBox>
                <RowList>
                    {
                        data && data.list.map((ele, index) => {
                            const {amount, time} = ele;
                            return (
                                <RowBox key={index}>
                                    <li>¥{amount}</li>
                                    <li>{time}</li>
                                </RowBox>
                            )
                        })
                    }
                </RowList>
                <ActivityIndicator
                    toast
                    text="Loading..."
                    animating={this.props.isRefreshing}
                />
            </Root>
        )
    }
}

Recharge = refreshAble('recharge')(Recharge);

export default Recharge;

const Root = styled.div`
    width: 100%;
    height: 100%;
`;

const HeadBox = styled.ul`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: row;
    border-bottom: .01rem solid #e1e1e1;
    li{
        width: 50%;
        height: .8rem;
        line-height: .8rem;
        color: #333333;
        text-align: center;
    }
`;

const RowList = styled.div`
    margin-top: 1rem;
`;

const RowBox = styled.ul`
    background-color: #fff;
    display: flex;
    flex-direction:row;
    margin-bottom: .05rem;
    li {
        width: 50%;
        height: 1rem;
        line-height: 1rem;
        color: #999999;
        text-align: center;
    }
`;


