export async function fetchDataWithPost(url: string, params: ParamsType, toast: boolean = true) {

    let header;
    let userId = params && params['userId'];
    if (userId) {
        header = {
            'Content-Type': 'application/json;charset=UTF-8',
            'userid': userId
        };
    } else {
        //create header
        header = {
            'Content-Type': 'application/json;charset=UTF-8'
        };
    }

    //以json字符串格式提交
    let bodyParams = {}, paramsStr;
    for (let key in params) {
        if (key != 'userId') {
            bodyParams[key] = params[key];
        }
    }
    paramsStr = JSON.stringify(bodyParams);

    //send post request
    const requestUrl = SERVER + url;
    let fetchPromise = fetch(requestUrl, {
        method: 'post',
        body: paramsStr,
        headers: header,
    }).then((response) => {
        if (response.ok) {
            return response.json()
        } else {
            throw new Error('网络错误')
        }
    }).then((model) => {
        if (model.success) {
            return model
        }
        else {
            return window.GoldWeb.handlerServiceError(model)
        }
    }).then((model) => {
        if (model && model.isRetry) {
            return fetchDataWithPost(url, {...params})
        }
        return model
    }).catch((error) => {
        if (toast && error.message) {
            Toast.info(error.message, 3, null, false);
        }
        throw error
    });

    // const abortable_promise = Promise.race([
    //     getTimeOutPromise(),
    //     fetchPromise,
    // ]).catch((error) => {
    //     if(toast && error.message) {
    //         Toast.info(error.message, 3, null, false);
    //     }
    //     throw error
    // })
    return await fetchPromise
}

export function fetchDataWithDataString(url: string, params: ParamsType, toast: boolean = true) {

    let header;
    let userId = params && params['userId'];
    if (userId) {
        header = {
            'Content-Type': 'application/x-www-form-urlencoded',
            'userid': userId
        };
    } else {
        //create header
        header = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
    }

    //create header
    /*const header = {
        'Content-Type': 'application/x-www-form-urlencoded'
    }*/

    //create formData
    //以key1=val1&key2=val2格式提交
    let dataString = '';
    let index = 0;
    const keys = Object.keys(params);
    keys.forEach((key) => {
        if (key != 'userId') {
            if (index != 0) {
                dataString += '&'
            }
            index++;
            const data = params[key];
            const encodeData = encodeURIComponent(data);
            dataString += `${key}=${encodeData}`
        }
    });

    //send post request
    const requestUrl = SERVER + url;
    let fetchPromise = fetch(requestUrl, {
        method: 'post',
        body: dataString,
        headers: header,
    }).then((response) => {
        if (response.ok) {
            return response.json()
        } else {
            throw new Error('网络错误')
        }
    }).then((model) => {
        if (model.success) {
            return model
        }
        else {
            return window.GoldWeb.handlerServiceError(model)
        }
    }).catch((error) => {
        if (toast && error.message) {
            Toast.info(error.message, 3, null, false);
        }
        throw error
    });

    // const abortable_promise = Promise.race([
    //     getTimeOutPromise(),
    //     fetchPromise,
    // ])
    return fetchPromise
}


export function fetchDataWithFormData(params: ParamsType, url: string) {
    //create header
    const header = {
        'Content-Type': 'multipart/form-data'
    }

    //create formData
    let formData = new FormData();
    const keys = Object.keys(params)
    keys.forEach((key) => {
        formData.append(key, params[key])
    })

    //send post request
    let fetchPromise = fetch(url, {
        method: 'post',
        body: formData,
        headers: header,
    }).then((response) => {
        if (response.ok) {
            return response.json()
        } else {
            throw new Error('网络错误')
        }
    }).then((model) => {
        return model
    }).catch((error) => {
        throw error
    })

    // const abortable_promise = Promise.race([
    //     getTimeOutPromise(),
    //     fetchPromise,
    // ])
    return fetchPromise
}

