import * as api from '@js/networking/Api.web'

//获取首页数据
export const requestHomeData = () => {
    return async function() {
        const userId = await window.GoldWeb.getUserId();
        return userId;
    }
};

//获取充值列表
export const requestRechargeList = () => {
    return async function() {
        const userId = await window.GoldWeb.getUserId();
        const model = await api.getRechargeList(userId);
        return model;
    }
};


