import React from 'react'
import styled from "styled-components";
import {InputItem, Toast} from "antd-mobile";
import {Field} from 'redux-form'

const InputItemBase = styled(InputItem).attrs({
    onChange: props => props.input.onChange,
    onBlur: props => props.input.onBlur,
    onFocus: props => props.input.onFocus,
    error: props => props.meta.invalid && props.meta.touched,
    onErrorClick: props => (() => Toast.info(props.meta.error, 3, null, false)),
})`
  height:  ${props => props.height || '.8rem !important'};
  input {
    font-size: ${props => props.fontSize || '0.3rem !important;'};
  }
  .am-input-label-5 {
    width: 0.8rem !important;
  }
`

export default class extends React.Component {
    render() {
        return (
            <Field
                {...this.props}
                component={InputItemBase}
            />
        )
    }
}


