//获取实名认证请求地址
export function getInsertInfo(
    bankNum: string,
    grade: string,
    idCard: string,
    mobile: string,
    realname: string,
    userId: string,
) {
    return fetchDataWithDataString(GetCustomInsertInfo,{
        bankNum: bankNum,
        grade: grade,
        idCard: idCard,
        mobile: mobile,
        realname: realname,
        userId: userId
    })
}


//注册获取手机验证码
export function getSendCode(mobile: string, validateKey: string) {
    return fetchDataWithDataString(GetSendCode, {
        mobile: mobile,
        validateKey: validateKey,
    }).then((model) => {
        if (model.msg) {
            Toast.info(model.msg, 3, null, false);
        }
        return model
    })
}

