import React from 'react'

import styled from 'styled-components'

import {NavBar} from 'antd-mobile'

import MainTab from '../mainTab/index.web'

import needTokenPage from '@js/component/needTokenPage'

class Home extends React.Component {

    constructor(props){
        super(props)
    }

    componentDidMount() {
        this._onRefresh()
            .catch((error) => {})
    }

    _onRefresh = async () => {
        return await window.GoldWeb.getUserId();
    };

    _jumpRoute = (type) => {
        if (type === 'webpack') {
            this.props.history.push('/Home/Webpack');
        } else if (type === 'page') {
            this.props.history.push('/Home/Page');
        } else if (type === 'mock') {
            this.props.history.push('/Home/Mock');
        } else if (type === 'storage') {
            this.props.history.push('/Home/Storage');
        }
    };

    render() {
        return(
            <Root>
                <NavBar mode="light">首页</NavBar>
                <ItemTitle>项目构建：</ItemTitle>
                <RowBox onClick={() => this._jumpRoute('webpack')}><label>webpack打包优化</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('page')}><label>页面布局及自适应</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('C')}><label>js sentry监控</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('mock')}><label>mock模拟请求</label></RowBox>
                <ItemTitle>业务模块：</ItemTitle>
                <RowBox onClick={() => this._jumpRoute('storage')}><label>数据持久化</label></RowBox>
                <ItemTitle>部署发布：</ItemTitle>
                <RowBox onClick={() => this._jumpRoute('D')}><label>Jenkins自动打包，部署</label></RowBox>
                <MainTab {...this.props}/>
            </Root>
        )
    }
}

Home = needTokenPage(Home);

export default Home;

const Root = styled.div`
    width:100%;
    height:100%;
`;

const ItemTitle = styled.div`
    height: .8rem;
    line-height: .8rem;
    padding-left: .3rem;
`;

const RowBox = styled.div`
    height: .8rem;
    line-height: .8rem;
    background-color: #fff;
    margin-bottom: .1rem;
    padding: 0 .3rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    label{color: #999999}
    ::after{
        content: '';
        width: .16rem;
        height: .29rem;
        margin-top: .25rem; 
        display: block;
        background: url(${props => props.imgUrl || require('@js/resources/arrow-right@2x.png')}) no-repeat center;
        background-size: 100%;
    }
`;


