import React from 'react'
import styled from 'styled-components'
import {Radio} from 'antd-mobile'
import {Field, change} from 'redux-form'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'

class RadioItem extends React.Component {

    state = {
        value: 1
    };

    static propTypes = {
        radioData: PropTypes.array,
    };

    componentDidMount() {
        const {input: {name}, meta: {form}} = this.props;
        this.props.changeFiledValue(form, name, this.state.value);
    }

    onChange = (value) => {
        const {input: {name}, meta: {form}} = this.props;
        this.props.changeFiledValue(form, name, value);
        this.setState({value});
    };

    render() {
        let _radioData = this.props.radioData;
        const { value } = this.state;

        return (
            <RadioBox>
                {
                    _radioData && _radioData.map((ele) => {
                        return (
                            <Radio
                                key={ele.value}
                                name={'sexOption'}
                                onChange={() => this.onChange(ele.value)}
                                checked={value === ele.value}>
                                {ele.label}
                            </Radio>
                        )
                    })
                }
            </RadioBox>
        )
    }
}

class RadioField extends React.Component {
    render(){
        return(
            <Field
                {...this.props}
                component={RadioItem}
            />
        )
    }
}

export default connect(
    null,
    (dispatch) => ({
        changeFiledValue: (form, filedName, value) => dispatch(change(form, filedName, value))
    })
)(RadioField);

const RadioBox = styled.div`
    &&& {
        .am-radio{
            padding: .05rem;
            border: 1px solid #ccc;
            border-radius: 50%;
        }
    }
`;



