//@flow

import {
    Request,
    Success,
    Failed,
    Cancle,
    HasMoreData,
} from './PullDownRefreshListActionTypes'

export function request(isRefresh: boolean, name: string, page: number, requestId: number) {
    return {
        type: Request,
        isRefresh,
        name,
        page,
        requestId,
    }
}

export function success(data: Object, isRefresh: boolean, name: string) {
    return {
        type: Success,
        data,
        isRefresh,
        name,
    }
}

export function failed(error: Error, name: string) {
    return {
        type: Failed,
        error,
        name,
    }
}

export function hasMoreData(hasMore: boolean, name: string) {
    return {
        type: HasMoreData,
        hasMore,
        name,
    }
}

let requestID = 0
export function cancle(name: string) {
    return {
        type: Cancle,
        requestID: ++requestID,
        name,
    }
}

//下拉刷新
function shouldRefreshList(state, name) {
    if (state[name].isRefreshing) {
        return false
    }
    return true
}


function refreshList(asyncApi: any, requestId: number, name: string) {
    return (dispatch: any, getState: any) => {
        const curRequestId = requestId + 1
        dispatch(request(true, name, 1, curRequestId))
        return asyncApi(1)
            .then((model)=>{
                if (curRequestId === getState()[name].requestId) {
                    const isLast = (model.list.length == 0)
                    dispatch(success(model.list, true, name))
                    dispatch(hasMoreData(!isLast, name))
                }
            })
            .catch((error)=>{
                if (curRequestId === getState()[name].requestId) {
                    dispatch(failed(error, name))
                }
            })
    }
}

export function refreshListIfNeeded(asyncApi: any, name: string) {
    return (dispatch: any, getState: any) => {
        if (shouldRefreshList(getState(), name)) {
            return dispatch(refreshList(asyncApi, getState()[name].requestId, name))
        } else {
            return Promise.resolve()
        }
    }
}

//上拉刷新
function shouldLoadMoreList(state, name) {
    if (state[name].isLoading) {
        return false
    } else if (!state[name].hasMore) {
        return false
    }
    return true
}

function loadMoreList(asyncApi: any, page: number, requestId: number, name: string) {
    return (dispatch: any, getState: any) => {
        const curRequestId = requestId + 1
        dispatch(request(false, name, page, curRequestId))
        return asyncApi(page)
            .then((model) => {
                if (curRequestId === getState()[name].requestId) {
                    const isLast = (model.list.length == 0)
                    dispatch(success(model.list, false, name))
                    dispatch(hasMoreData(!isLast, name))
                }
            })
            .catch((error) => {
                if (curRequestId === getState()[name].requestId) {
                    dispatch(failed(error, name))
                }
            })
    }
}

export function loadMoreListIfNeeded(asyncApi: any, name: string) {
    return (dispatch: any, getState: any) => {
        const state = getState()
        if (shouldLoadMoreList(state, name)) {
            const nextPage = state[name].curPage + 1
            return dispatch(loadMoreList(asyncApi, nextPage, state[name].requestId, name))
        } else {
            return Promise.resolve()
        }
    }
}





