import styled from 'styled-components'

const enableImage = `url(${require('./form/resources/web/loadingbutton.png')})`
const disableImage = `url(${require('./form/resources/web/loadingbutton-gray.png')})`

const Button = styled.button.attrs({
    onClick: props => (props.enable === false ? null : props.onClik)
})`
  ${
    (props) =>
        props.backgroundColor ?
            `background-color: ${props.enable === false ? '#e1e1e1' : props.backgroundColor};` :
            `background-image: ${props.enable === false ? disableImage : enableImage};`
    }
    display: block;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    text-align: center;
    color: ${props => props.color || '#FFFFFF'};
    width: ${props => props.width || '5.96rem'};
    height: ${props => props.height || '0.9rem'};
    font-size: ${props => props.fontSize || '0.32rem'};
    border-radius: ${props => props.borderRadius || '0'};
    ${props => props.style || ''}
`
// background-color: ${props => props.enable === false ? '#e1e1e1' : (props.backgroundColor || '#00000000')};
export default Button


