//获取URL参数
export function GetRequest() {
    var url = window.location.search; //获取url中"?"符后的字串
    var theRequest = new Object();
    var strs = [];
    if (url.indexOf("?") != -1) {
        var str = url.substr(1);
        strs = str.split("&");
        for(var i = 0; i < strs.length; i ++) {
            theRequest[strs[i].split("=")[0]]=unescape(strs[i].split("=")[1]);
        }
    }
    return theRequest;
}

//正则 匹配11位手机号首尾中间用*代替
export function regMiddleReplaceMobile(num){
    return num && num
        .toString()
        .replace(/(\d{3})\d{4}(\d{4})/,'$1****$2')
}
//正则 匹配18位身份证号中间用*代替
export function regMiddleReplaceIDCard(num){
    return num && num
        .toString()
        .replace(/(\d{6})\d{8}(\d{4})/,'$1********$2')
}

//正则 匹配位数不一样的银行卡号中间用*代替
export function regMiddleReplaceBankCard(num){
    if (num) {
        const len = num.toString().length
        const midNum = len - 8
        let asterisk = ''
        for (let i = 0; i < midNum; i++) {
            asterisk += '*'
        }
        return num.toString().replace(/(\d{4})\d*(\d{4})/,'$1'+asterisk+'$2')
    }
}

// 正则 千位符
export function thousandBitSeparator(num) {
    return num && num
        .toString()
        .replace(/(^|\s)\d+/g, (m) => m.replace(/(?=(?!\b)(\d{3})+$)/g, ','))
}

export function getQueryStringArgs(url) {
    url = url == null ? window.location.href : url;
    var qs = url.substring(url.lastIndexOf("?") + 1);
    var args = {};
    var items = qs.length > 0 ? qs.split('&') : [];
    var item = null;
    var name = null;
    var value = null;
    for (var i = 0; i < items.length; i++) {
        item = items[i].split("=");
        //用decodeURIComponent()分别解码name 和value（因为查询字符串应该是被编码过的）。
        name = decodeURIComponent(item[0]);
        value = decodeURIComponent(item[1]);

        if (name.length) {
            args[name] = value;
        }
    }
    return args;
}

/**
 *  判断传入参数的类型，以字符串的形式返回
 *  @obj：数据
 **/
function dataType(obj){
    if (obj===null) return "Null";
    if (obj===undefined) return "Undefined";
    return Object.prototype.toString.call(obj).slice(8,-1);
};

//处理对象参数值，排除对象参数值为”“、null、undefined，并返回一个新对象
export function dealObjectValue(obj){
    var param = {};
    if ( obj === null || obj === undefined || obj === "" ) return param;
    for ( var key in obj ){
        if ( dataType(obj[key]) === "Object" ){
            param[key] = dealObjectValue(obj[key]);
        }else if(  obj[key] !== null && obj[key] !== undefined && obj[key] !== ""  ){
            param[key] = obj[key];
        }
    }
    return param;
};

/**
 *  判断传入参数的类型，以字符串的形式返回
 *  @obj：数据
 **/
function dataType(obj){
    if (obj===null) return "Null";
    if (obj===undefined) return "Undefined";
    return Object.prototype.toString.call(obj).slice(8,-1);
};

export function formatDate (date, fmt) {
    var o = {
        "M+": date.getMonth() + 1, //月份
        "d+": date.getDate(), //日
        "h+": date.getHours(), //小时
        "m+": date.getMinutes(), //分
        "s+": date.getSeconds(), //秒
        "q+": Math.floor((date.getMonth() + 3) / 3), //季度
        "S": date.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

/**
 * 使用循环的方式判断一个元素是否存在于一个数组中
 * @param {Object} arr 数组
 * @param {Object} value 元素值
 */
export function isInArray(arr,value){
    for(var i = 0; i < arr.length; i++){
        if(value === arr[i]){
            return true;
        }
    }
    return false;
}


