//@flow

import {
    FetchDataRequest,
    FetchDataSuccess,
    FetchDataFailed,
    FetchDataCancle,
} from './RefreshActionTypes'

export function fetchDataRequest (identifier: string) {
    return {
        type: FetchDataRequest,
        identifier,
    }
}

export function fetchDataSuccess (model: Object, identifier: string) {
    return {
        type: FetchDataSuccess,
        model,
        identifier,
    }
}

export function fetchDataFailed (error: Object, identifier: string) {
    return {
        type: FetchDataFailed,
        error,
        identifier,
    }
}

export function fetchDataCancle (identifier: string) {
    return {
        type: FetchDataCancle,
        identifier,
    }
}

function shouldFetchData(state: Object, identifier: string) {
    const refreshData = state.refreshAble.get(identifier)
    if (!refreshData) {
        return true
    } else if (refreshData.isRefreshing) {
        return false
    }
    return true
}

function fetchData(asyncApi: any, identifier: string) {
    return (dispatch: any, getState: any) => {
        dispatch(fetchDataRequest(identifier))

        return asyncApi()
            .then((model) => {
                const state = getState()
                if (state.refreshAble.get(identifier)) {
                    dispatch(fetchDataSuccess(model, identifier))
                }
            })
            .catch((error) => {
                const state = getState()
                if (state.refreshAble.get(identifier)) {
                    dispatch(fetchDataFailed(error, identifier))
                }
            })
    }
}

//获取数据
export function fetchDataIfNeeded (asyncApi: any, identifier: string) {
    return (dispatch: any, getState: any) => {
        if (shouldFetchData(getState(), identifier)) {
            return dispatch(fetchData(asyncApi, identifier))
        } else {
            return Promise.resolve()
        }
    }
}


