import React from 'react'
import styled from 'styled-components'
import { Picker, List } from 'antd-mobile'
import PropTypes from 'prop-types'
import { Field, change } from 'redux-form'
import { connect } from 'react-redux'

class PickerItem extends React.Component {

    static propTypes = {
        initialValue: PropTypes.any,
        pickerData: PropTypes.any,
        extraValue: PropTypes.any,
        disabled: PropTypes.bool,
    };

    constructor() {
        super(...arguments);

        this.state = {
            data: this.props.pickerData || [],
            sValue: this.props.initialValue || [],
            extra: this.props.extraValue || '',
            disabled: this.props.disabled || false,
        }
    }

    _onChange = (v) => {
        const {input: {name}, meta: {form}} = this.props;
        this.props.changeFiledValue(form, name, v);
        this.setState({ sValue: v });
    };

    render() {

        return (
            <PickerBox disabled={this.state.disabled}>
                <Picker
                    data={this.state.data}
                    cols={1}                   //表示弹层后显示的列数
                    value={this.state.sValue}
                    extra={this.state.extra}
                    disabled={this.state.disabled}
                    onChange={this._onChange}
                    onOk={this._onChange}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </Picker>
            </PickerBox>
        )
    }
}

class PickerField extends React.Component {

    constructor() {
        super(...arguments)
    }

    render() {
        return (
            <Field
                {...this.props}
                component={PickerItem}
            />
        )
    }
}

export default connect(
    null,
    (dispatch) => ({
        changeFiledValue: (form, filedName, value) => dispatch(change(form, filedName, value))
    })
)(PickerField);

const PickerBox = styled.div`
    &&& {
        .am-list-item{
            border: .02rem solid #e1e1e1;
            border-radius: .06rem;
            background-color: ${props => props.disabled ? '#e1e1e1' : 'transparent'};
        }
        .am-list-extra{
            flex-basis: 100%;
            text-align: left;
            font-size: .28rem;
        }
    }
`;


