//@flow

import React, {PureComponent} from 'react'

import PropTypes from 'prop-types'

import { PullToRefresh, ListView, ActivityIndicator } from 'antd-mobile';

import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from './PullDownRefreshListActions'
import ContentLoader from 'react-content-loader'


class PullDownRefreshListWeb extends PureComponent {
    static propTypes = {
        asyncApi: PropTypes.any.isRequired,
        identifier: PropTypes.string.isRequired,
        noDataRender: PropTypes.any,
        contentLoader: PropTypes.any,
    }

    state: Object;
    dataSource: any;

    constructor() {
        super(...arguments)

        this.dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
    }

    componentDidMount() {
        this._onRefresh()
    }

    componentWillUnmount() {
        this.props.cancle(this.props.identifier)
    }

    _onRefresh = () => {
        //下拉刷新
        this.props.refreshListIfNeeded(this.props.asyncApi, this.props.identifier)
    }

    _onEndReached = (info: {distanceFromEnd: number}) => {
        if (this.props && this.props.hasMore) {
            this.props.loadMoreListIfNeeded(this.props.asyncApi, this.props.identifier)
        }
    }

    _separator = (sectionID, rowID) => (
        <div
            key={`${sectionID}-${rowID}`}
            style={{
                backgroundColor: '#F5F5F9',
                height: '0.3rem',
                borderTop: '1px solid #ECECED',
                borderBottom: '1px solid #ECECED',
            }}
        />
    )

    _renderFooter = () => (<div style={{ padding: 30, textAlign: 'center' }}>
        {this.props.loadMore ? '正在加载...' : '加载完成'}
    </div>)

    render(){
        const data = this.props.data;
        //console.log(data);
        if (data && data.length > 0) {
            this.dataSource = this.dataSource.cloneWithRows(data)
            return (
                <ListView
                    dataSource={this.dataSource}            //数据源
                    renderFooter={this._renderFooter}       //页脚
                    renderSeparator={this._separator}       //行之间的小组件（间隔）
                    pullToRefresh={<PullToRefresh           //拉动刷新，配合PullToRefresh组件使用
                        refreshing={this.props.refreshing}
                        onRefresh={this._onRefresh}
                    />}
                    onEndReached={this._onEndReached}       //当所有的数据都已经渲染过，并且列表被滚动到距离最底部不足onEndReachedThreshold个像素的距离时调用
                    onEndReachedThreshold={0.1}             //调用onEndReached之前的临界值，单位是像素
                    distanceToRefresh={window.devicePixelRatio * 25 * 0.5}
                    {...this.props}
                />
            )
        } else {
            if (this.props.refreshing) {
                return (
                    <ContentLoaderBox {...this.props}/>
                )
            } else {
                return this.props.noDataRender;
            }
        }
    }
}

class ContentLoaderBox extends React.Component {
    render() {
        let contentMap = [1,2,3,4,5,6,7,8,9];

        return (
            <div style={{width: '100%',height: '100%'}}>
                {
                    contentMap.map((ele,index) => {
                        if (this.props.contentLoader) {
                            return (
                                <div key={index}>{this.props.contentLoader}</div>
                            )
                        } else {
                            return (
                                <ContentLoader height={68} style={{backgroundColor: '#fff',marginBottom: '0.3rem',padding: '.1rem 0'}} key={index}>
                                    <rect x="10" y="3" rx="5" ry="5" width="60" height="60" />
                                    <rect x="80" y="17" rx="4" ry="4" width="280" height="13" />
                                    <rect x="80" y="40" rx="3" ry="3" width="220" height="10" />
                                </ContentLoader>
                            )
                        }
                    })
                }
                {/*<ActivityIndicator
                    toast
                    text="获取列表..."
                    animating={this.props.refreshing}
                />*/}
            </div>
        )
    }
}

function mapStateToProps(state, oriProps:Object) {
    return {
        refreshing: state[oriProps.identifier].get('isRefreshing'),
        loadMore: state[oriProps.identifier].get('isLoading'),
        hasMore: state[oriProps.identifier].get('hasMore'),
        data: state[oriProps.identifier].get('list'),
        ...oriProps
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(actions, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(PullDownRefreshListWeb)


