import React from 'react'
import styled from "styled-components";
import {Toast} from "antd-mobile";
import {Field} from 'redux-form'
import './yidun.css'

class CstaticdunCode extends React.Component {
    constructor() {
        super(...arguments)

        this._instance = null
    }

    componentDidMount() {
        this._initNECaptcha()
    }

    componentDidUpdate(prevProps) {
        if(!prevProps.meta.submitting && this.props.meta.submitting) {
            //触发了提交表单的动作,刷新验证码
            this._instance.refresh()
        }
    }

    _initNECaptcha = () => {
        const {input: {onChange}} = this.props
        if(window.initNECaptcha) {
            window.initNECaptcha({
                // config对象，参数配置
                captchaId: process.env.__DEV__ ? '8cc7201be06d4b74b8b59f09bf1a3fe8' : '97357bd433364651a4c09dd71e86449e',
                element: '#captcha',
                mode: 'float',
                width: '100%',
                onVerify: function (err, data) {
                    /**
                     * 第一个参数是err（Error的实例），验证失败才有err对象
                     * 第二个参数是data对象，验证成功后的相关信息，data数据结构为key-value，如下：
                     * {
                     *   validate: 'xxxxx' // 二次验证信息
                     * }
                     */
                    if(err) {
                        onChange(null)
                        Toast.fail('验证失败', 3, null, false)
                    } else {
                        onChange(data.validate)
                        Toast.info('验证通过', 3, null, false)
                    }
                }
            }, (instance) => {
                // 初始化成功后得到验证实例instance，可以调用实例的方法
                this._instance = instance
            }, function (err) {
                // 初始化失败后触发该函数，err对象描述当前错误信息
                Toast.fail('易盾初始化失败', 3)
            })
        }
    }

    render() {
        return (
            <div id="captcha" style={{marginTop:'.5rem'}}/>
        )
    }
}

export default class extends React.Component {
    render() {
        return (
            <Field
                {...this.props}
                component={CstaticdunCode}
            />
        )
    }
}


