import React from 'react'
import InputItemField from './InputItemField.web'
import {
    CDManagerActions,
} from '../cdManager'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {
    ActivityIndicator,
    Flex,
} from 'antd-mobile'
import styled from "styled-components";
import { tintColor } from '../../commom/styles';

class VCCodeFiled extends React.Component {
    constructor() {
        super(...arguments)

        this.state = {
            isLoading: false
        }
    }

    componentWillUnmount() {
        this.interval && clearInterval(this.interval)
    }

    _onClick = () => {
        const {isInCd, enable} = this.props

        if (!isInCd && enable) {
            this.setState({isLoading: true})
            this.props.onClick()
                .then((response) => {
                    this.setState({isLoading: false})
                    this._shouldStartCountting(response.result)
                })
                .catch(() => {
                    this.setState({isLoading: false})
                })
        }
    }

    _shouldStartCountting = (shouldStart) => {
        if (this.props.isInCd) {
            return
        }
        if (shouldStart) {
            this.props.startCD(this.props.cdKey)
            this._countDownAction()
        }
    }

    //倒计时
    _countDownAction = () => {
        this.interval && clearInterval(this.interval)
        this.props.countCD(this.props.cdKey)
        this.interval = setInterval(() => {
            this.props.countCD(this.props.cdKey)
        }, 1000)
    }

    _renderExtra = () => {
        if (this.state.isLoading) {
            return (
                <View>
                    <ActivityIndicator/>
                </View>
            )
        }

        const {subSecond, isInCd, enable} = this.props
        const title = isInCd ? `重新获取(${subSecond}s)` : '获取验证码'
        console.log('重新获取='+subSecond);

        if (isInCd && !this.interval) {
            this._countDownAction()
        }
        !isInCd && this.interval && clearInterval(this.interval)

        return (
            <View>
                <Text enable={(!isInCd && enable)}>{title}</Text>
            </View>
        )
    }

    render() {
        const {name, placeholder} = this.props
        return (
            <InputItemField
                name={name}
                placeholder={placeholder}
                extra={this._renderExtra()}
                onExtraClick={this._onClick}
            />
        )
    }
}

function mapStateToProps(state, oriProps) {
    return {
        subSecond: state.cdManager.get(oriProps.cdKey) ? state.cdManager.get(oriProps.cdKey).subSecond : 0,
        isInCd: state.cdManager.get(oriProps.cdKey) ? state.cdManager.get(oriProps.cdKey).isInCd : false,
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(CDManagerActions, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(VCCodeFiled)

const Text = styled.p`
    font-size: 0.3rem;
    color: ${props => props.enable ? tintColor : '#e1e1e1'};
    text-align: center;
`

const View = styled(Flex).attrs({
    direction: 'row',
    justify: 'center',
    align: 'center',
})`
    width: 2.3rem;
    position:relative;
    &:after{
        content: '';
        width:2px;
        height:0.3rem;
        border-left: 1px solid #e1e1e1;
        position:absolute;
        top:0;
        left:0;
    }
`


