import React from 'react'

import styled from 'styled-components'

import {Route, Switch} from 'react-router-dom'

import Home from './Home'
import Webpack from "./Webpack";
import Page from './Page';
import Mock from './Mock';
import Recharge from './Recharge';
import Storage from './Storage';

export default class extends React.Component {
    render() {
        return(
            <Root>
                <Switch location={this.props.location}>
                    <Route exact path={`${this.props.match.path}/`} component={Home}/>
                    <Route path={`${this.props.match.path}/Webpack`} component={Webpack}/>
                    <Route path={`${this.props.match.path}/Page`} component={Page}/>
                    <Route path={`${this.props.match.path}/Mock`} component={Mock}/>
                    <Route path={`${this.props.match.path}/Recharge`} component={Recharge}/>
                    <Route path={`${this.props.match.path}/Storage`} component={Storage}/>
                </Switch>
            </Root>
        )

    }
}

const Root = styled.div`
    width:100%;
    height:100%;
`;


