import React from 'react'
import InputItemField from './InputItemField.web'

export default class extends React.Component {
    constructor() {
        super(...arguments)

        this.state = {
            type: 'password'
        }
    }

    _onClick = () => {
        this.setState({
            type: this.state.type === 'text' ? 'password' : 'text'
        })
    }

    _renderExtra = () => {
        const imageSource = this.state.type === 'password' ? require('./resources/web/biyan.png') : require('./resources/web/eye.png')
        return (
            <img style={{width: '0.32rem', height: '0.15rem'}} src={imageSource}/>
        );
    }

    render() {
        return (
            <InputItemField
             {...this.props}
              type={this.state.type}
              extra={this._renderExtra()}
              onExtraClick={this._onClick}
            />
        )
    }
}


