import React from 'react'

import styled from 'styled-components'



export default class Webpack extends React.Component {
    render() {
        return(
            <Root>
                <div>
                    <Title>webpack打包优化包括四个部分：</Title>
                    <Text>1.拆分node_modules包及业务代码</Text>
                    <Text>2.分离node_modules比较大的包</Text>
                    <Text>3.拆分路由，代码分割</Text>
                    <Text>4.GZIP压缩</Text>
                    <Text>具体操作：<a href="https://github.com/aichen28/react_template/blob/master/doc/webpack.md" target="_blank">webpack打包优化</a></Text>
                    <Title>查看打包效果：</Title>
                    <Text>1.执行命令：npm run webBuildDev（测试），npm run webBuildProduction（正式）</Text>
                    <Text>2.全局安装serve，用serve开启本地服务，以访问web/build打包好的文件，命令：serve -s web/build，默认访问地址:"本地ip:5000"</Text>
                </div>
            </Root>
        )
    }
}

const Root = styled.div`
    width:100%;
    height:100%;
    background-color: #fff;
`;

const Title = styled.h3`
    color: #333333;
    padding-top: .3rem;
    padding-bottom: .2rem;
    text-indent: .3rem;
`;

const Text = styled.p`
    color: #999999;
    line-height: .4rem;
    text-indent: .3rem;
    a{color: #11b6f8;}
`;


