import React from 'react'

import styled from 'styled-components'



export default class Page extends React.Component {
    render() {
        return(
            <Root>
                <Title>#样式单位采用：rem</Title>

                <Title>#使用阿里适配高清方案</Title>

                <Title>#UI采用styled-components + antd-mobile实现</Title>

                <Text>
                    部分讲解请参考：
                    <a target="_blank" href="https://github.com/aichen28/react_template/blob/master/doc/rem.md">页面布局及自适应</a>
                </Text>
            </Root>
        )
    }
}

const Root = styled.div`
    width:100%;
    height:100%;
    background-color: #fff;
`;

const Title = styled.h3`
    color: #333333;
    padding-top: .3rem;
    padding-bottom: .2rem;
    text-indent: .3rem;
`;

const Text = styled.p`
    color: #999999;
    line-height: .4rem;
    text-indent: .3rem;
    a{color: #11b6f8;}
`;


