import React from 'react'
import {connect} from 'react-redux'

export default function(WrappedComponent) {
    return connect(
        (state) => ({
            wattingRestore: state.tokenReducer.wattingRestore
        })
    )(class extends React.Component {
        render() {
            //在执行刷新页面时wattingRestore（等待恢复）为true，不返回当前组件，等恢复数据后再返回当前组件
            //因为在当前组件渲染后会请求数据，在请求数据时会通过window.GoldWeb.getUserId()判断是否有登录，如果先返回了当前组件而数据还在等待恢复阶段，就会一直提示未登录
            if(this.props.wattingRestore) return null;
            return <WrappedComponent {...this.props}/>
        }
    })
}


